///icon font
//this us used because the gulp icon font generation 
//isnt great, can be removed if we make the icons set 
//a third part plugins

.icon-font {}

.icon-font-text {
    position: absolute;
    left: -10000px;
    top: auto;
    width: 1px;
    height: 1px;
    overflow: hidden;
}

[class^="tk-icon-"], [class*=" tk-icon-"], .tk-icon {
    /* use !important to prevent issues with browser extensions that change fonts */    
    speak: none;
    font-style: normal;
    font-weight: normal;
    font-variant: normal;
    text-transform: none;
    line-height: 1;

    /* Better Font Rendering =========== */
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}