/**
 * Import Variables
 */

@import "toolkit/vars"; //Custom vars that override bootstrap vars
@import "toolkit/foundations/tk-icons"; //we need to extend icons for modal
@import "toolkit/foundations/tk-icons-helper"; 
@import "accomodation/site-footer"; //Slightly different footer
@import "accomodation/residence-map"; //Custom google maps
@import "accomodation/lightbox"; //Image gallery lightbox
@import "accomodation/icon-list"; // List of inline icons
@import "accomodation/vertical-divider"; // List of inline icons


////lightbox for accomodation

.ekko-lightbox .glyphicon-chevron-right {
    &:before {          
        @extend .tk-icon;   
        @extend .tk-icon-chevron-right:before;    
    }
}

.ekko-lightbox .glyphicon-chevron-left {
    &:before {          
        @extend .tk-icon;   
        @extend .tk-icon-chevron-left:before;    
    }
}


//temporary style to invert the image icons to white on the single residence slider - DELETE this class after the images have been converted to white
.inverted {
    filter: invert(100%);
    -webkit-filter: invert(1);
}
