@font-face {
	font-family: "toolkit-icons";
	src: url('../fonts/toolkit-icons.eot');
	src: url('../fonts/toolkit-icons.eot?#iefix') format('eot'),
		url('../fonts/toolkit-icons.woff2') format('woff2'),
		url('../fonts/toolkit-icons.woff') format('woff'),
		url('../fonts/toolkit-icons.ttf') format('truetype'),
		url('../fonts/toolkit-icons.svg#toolkit-icons') format('svg');
}

@mixin tk-icon-styles {
	font-family: "toolkit-icons";
		-webkit-font-smoothing: antialiased;
		-moz-osx-font-smoothing: grayscale;
	font-style: normal;
	font-variant: normal;
	font-weight: normal;
	// speak: none; // only necessary if not using the private unicode range (firstGlyph option)
	text-decoration: none;
	text-transform: none;
}

%tk-icon {
	@include tk-icon-styles;
}

@function tk-icon-char($filename) {
	$char: "";

	@if $filename == add {
		$char: "\E001";
	}
	@if $filename == alert-danger {
		$char: "\E002";
	}
	@if $filename == alert-info {
		$char: "\E003";
	}
	@if $filename == alert-success {
		$char: "\E004";
	}
	@if $filename == alert-warning {
		$char: "\E005";
	}
	@if $filename == calendar {
		$char: "\E006";
	}
	@if $filename == campus-map {
		$char: "\E007";
	}
	@if $filename == chevron-down {
		$char: "\E008";
	}
	@if $filename == chevron-left {
		$char: "\E009";
	}
	@if $filename == chevron-right {
		$char: "\E00A";
	}
	@if $filename == chevron-up {
		$char: "\E00B";
	}
	@if $filename == close {
		$char: "\E00C";
	}
	@if $filename == download {
		$char: "\E00D";
	}
	@if $filename == external {
		$char: "\E00E";
	}
	@if $filename == home {
		$char: "\E00F";
	}
	@if $filename == letter {
		$char: "\E010";
	}
	@if $filename == mail {
		$char: "\E011";
	}
	@if $filename == marker {
		$char: "\E012";
	}
	@if $filename == navicon {
		$char: "\E013";
	}
	@if $filename == news {
		$char: "\E014";
	}
	@if $filename == phone {
		$char: "\E015";
	}
	@if $filename == print {
		$char: "\E016";
