	@if $filename == profile {
		$char: "\E017";
	}
	@if $filename == remove {
		$char: "\E018";
	}
	@if $filename == rss {
		$char: "\E019";
	}
	@if $filename == search {
		$char: "\E01A";
	}
	@if $filename == share {
		$char: "\E01B";
	}
	@if $filename == social-facebook {
		$char: "\E01C";
	}
	@if $filename == social-google {
		$char: "\E01D";
	}
	@if $filename == social-instagram {
		$char: "\E01E";
	}
	@if $filename == social-linkedin {
		$char: "\E01F";
	}
	@if $filename == social-twitter {
		$char: "\E020";
	}
	@if $filename == social-weibo {
		$char: "\E021";
	}
	@if $filename == social-youtube {
		$char: "\E022";
	}
	@if $filename == sort {
		$char: "\E023";
	}
	@if $filename == star {
		$char: "\E024";
	}
	@if $filename == triangle-down {
		$char: "\E025";
	}
	@if $filename == triangle-left {
		$char: "\E026";
	}
	@if $filename == triangle-right {
		$char: "\E027";
	}
	@if $filename == triangle-up {
		$char: "\E028";
	}

	@return $char;
}

@mixin tk-icon($filename, $insert: before, $extend: true) {
	&:#{$insert} {
		@if $extend {
			@extend %tk-icon;
		} @else {
			@include tk-icon-styles;
		}
		content: tk-icon-char($filename);
	}
}

